
.record{
		font-size: 1.6vw;
}
.record div.head{
			position: absolute;
    	top: 0rem;
    	left: 230px;
}
.record div.head .back{
		top: 50%;
		left:0;
    -webkit-transform: translateY(-45%);
    transform: translateY(-45%);
}
input,select{
		font-size: 16px;
}
.cov-date-body{
}
.record-head{
		line-height: 81px;
		padding-left: 350px;
}
.record-head-left{
    margin-left: 20px;
    margin-right: 10px;
}
.record-head>div {
    display: inline-block;
    margin-right: 20px;
}
.record .name-text{
		display: inline-block;
    height: 40px;
    margin-left: 14px;
}
.record .name-text input{
		height: 40px;
    width: 150px;
		padding-left: 10px;
    border: 1px solid #999;
}
.record .time,.record .status-text{
		display: inline-block;
		margin-left: 14px;
}
.status-select{
		outline: none;
    border: 0;
    background: #fff;
    border: 1px solid #999;
    height: 40px;
    width: 150px;
		padding-left: 10px;
		-webkit-box-sizing: border-box;
		        box-sizing: border-box;
}
.record-content{
    padding: 0px 1%;
    padding-left: 230px;
    padding-right: 30px;
}
.record-content-head{
		background: #f5faff;
    line-height: 60px;
    border-radius: 5px;
}
.record-list{
		padding-top: 10px;
		overflow: auto;
}
.record-content-head>div,.record-item>div {
	    display: inline-block;
	    text-align: center;
}
.make-time,.make-paly,.make-bumber{
	 	    width:20%;
}
.make-doctor{
	 	width: 14%;
}
.make-name,.make-status{
	 	width: 13%;
}
.record-item{
	 		line-height: 60px;
	 		font-size: 1.5vw;
	 		padding: 8px 0;
}
.make-operate span{
	 		display: inline-block;
	    background: #fc9453;
	    line-height: 55px;
	    width: 85%;
	    color: #fff;
	    border-radius: 5px;
}
.make-operate .cancelbtn{
	 	color: #fc9453;
    background: #fff;
    border: 1px solid #fc9453;
}
.make-operate .waiting{
 	    background: #3C9BFF;
}
.make-status.done{
	 	color: #3C9BFF;
}
.make-operate.done span{
	 		background: #fc9453;
}
.make-statu.unfinished{
	 	 color: #d43c33;
}
.make-operate.unfinished span{
	 		background: #3C9BFF;
}
.make-status .Finish{
	 	color: #333;
}
.make-status .Pending{
	 	color: #3C9BFF;
}
.make-status .Canceled{
	 	color: #999;
}
.make-status .Unpaid{
	 	color: #FC9452 ;
}
.make-operate .Pending{
	 		background:#3C9BFF;
}
.make-operate .Pending.overdue,.make-operate .Canceled{
	 		background:#eee;
	 		color: #999;
}
.cov-date-caption{
	 	color:#3C9BFF !important;
}
.checked{
	 	background:#3C9BFF !important;
}
.cov-datepicker{
	 	border-color:#999 !important;
	 	padding: 0 0 0 10px !important;
		-webkit-box-sizing: border-box;
		        box-sizing: border-box;
	 	width: 150px !important;
	 	height: 40px !important;
	 	line-height: 40px !important;
}
.cov-date-caption{
	 	font-size: 2vw !important;
	 	line-height: 1;
}
.cov-date-body,.cov-picker-box{
	 	width: 647px !important;
}
.cov-picker-box{
	 	height: 542px !important;
}
.day{
	 	width: 65px !important;
	 	height:60px !important;
    line-height: 60px !important;
    font-size: 23px;
}
.week ul{
 	    margin: 0 0 -20px !important;
}
.cov-picker-box{
	 	    padding: 10px 25px !important;
}
.cov-date-box{
	 	    background: #fff !important;
}
.cov-date-monthly{
	 	 height: 100px !important;
}
.cov-date-monthly > div{
 	    height: 100px !important;
}
.cov-date-body .cov-date-monthly .cov-date-caption{
 	    padding: 30px 0!important;
}
.record-head-center{
	 	position: relative;
}
.opcinput{
	 	position: absolute;
    top: 0;
    left: 50px;
    z-index: 1;
	 	opacity: 0;
}
.hideinput{
    height: 40px;
    width: 140px;
    padding-left: 10px;
    border: 1px solid #999;
}
	/**/
.win-video{
	  position: fixed;
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,0.7);
    top: 0;
    left: 0;
    z-index: 2;
}
.win-video-content{
    width: 70%;
    margin-left: 15%;
    margin-top: 10%;
    background: #fff;
    border-radius: 10px;
    font-size: .32rem;
    position: relative;
}
.video-content-left{
		width: 40%;
    text-align: center;
    padding-top: 45px;
    height: 100%;
    background: #ebf5ff;
    padding-bottom: 45px;
    border-radius: 10px 0 0 10px;
    min-height: 370px;
}
.win-video-content>div {
    display: inline-block;
    vertical-align: text-top;
}
.video-left-bottom{
		background: #fff;
    width: 80%;
    margin-left: 10%;
    border-radius: 7px;
    margin-top: 30px;
    padding-bottom: 30px;
}
.video-left-bottom>p{
		margin-bottom: 10px;
}
.vi-icon{
		width: auto;
		height: 1.1rem;
		margin-top: -0.4rem;
    border-radius: 50%;
}
.vd-nam{
		font-size: 1.8vw;
    font-weight: bold;
}
.vi-status{
    background: #e9f4de;
		color: #4b9705;
    padding: 3px 5px;
}
.video-content-roght{
    text-align: center;
    width: 60%;
    padding: 30px 0;
}
.win-video-content>div {
    display: inline-block;
    vertical-align: text-top;
}
.wait-time,.wait-time-text{
		color: #d43c33;
		font-size: 0.36rem;
}
.patient-mess{
		padding: 0px 20%;
}
.patient-mess>p{
		border-bottom: 1px solid #eee;
    padding: 20px 0;
    font-size: 0.7rem;
}
.patient-mess>p img{
		width: 1.15rem;
    height: 1.15rem;
    vertical-align: middle;
    margin-right: 15px;
    border-radius: 50%;
}
.wait-detail{
    padding: 20px 0 35px 0;
    font-size: .4rem;
}
.wait-detail>p span{
		color: #F59C31;
}
.wait-detail>p span:nth-child(1){
		font-size: 1rem;
}
.wait-success{
		padding: 30px 0;
    font-size: 1rem;
    color: #3C9BFF;
}
.wait-hint{
		text-align: center;
}
.wait-hint p{
		display: inline-block;
    width: 45%;
    background: #3C9BFF;
    color: #fff;
    line-height: 1rem;
    border-radius: 10px;
    font-size: .48rem;
}
.wait-btn>p{
   	display: inline-block;
    line-height: 1rem;
}
.canceljoin{
		width: 20%;
    border: 1px solid #F59C31;
    color: #F59C31;
    border-radius: 5px;
    font-size: .48rem;
    margin-right: 10%;
}
.joinroom{
		width: 45%;
    background: #3C9BFF;
    color: #fff;
    line-height: 1rem;
    border-radius: 5px;
    font-size: .48rem;
}
.vi-close{
		position: absolute;
    top: 10px;
    right: 15px;
}
.vi-close img{
		width: auto;
    height: .6rem;
}
	/**/
.win-cancel{
		position: fixed;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    background: rgba(0,0,0,0.6);
}
.win-cancel-content{
		position: absolute;
    width: 56%;
    left: 22%;
    background: #fff;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    border-radius: 10px;
    text-align: center;
    padding-top: .45rem;
}
.cancel-hint{
		font-size: 0.44rem;
    font-weight: bold;
}
.cancel-hint-text{
		padding: .5rem .2rem;
    font-size: .38rem;
    border-bottom: 1px solid #eee;
    color: #666;
}
.win-cancel-btn>div {
    display: inline-block;
    width: 50%;
    line-height: 1.08rem;
    font-size: .4rem;
}
.cancel-btn-left{
		border-right: 1px solid #eee;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    color: #666;
}
.cancel-btn-right{
		color: #3C9BFF;
}
